
import store from "@/store";
import { defineComponent } from "@vue/runtime-core";
import dayjs from "dayjs";

export default defineComponent({
  computed: {
    careers() {
      return store.state.careers;
    },
  },
  methods: {
    calculateDate(dateString: string): string {
      const splitDateString = dateString.split("~");

      if (splitDateString.length > 1 && splitDateString[0]) {
        const dateStr = splitDateString[0].split(" ");

        const year = dateStr[0].match(/\d+/g);
        const month = dateStr[1].match(/\d+/g);

        if (year && month) {
          const parseDate = dayjs()
            .set("year", +year)
            .set("month", +month)
            .set("date", 1);

          const dateStr2 = splitDateString[1].replace(/\s/g, "");

          if (dateStr2 == "현재") {
            const yearDiff = dayjs().diff(parseDate, "year");
            const diff = dayjs().diff(parseDate, "month");

            if (yearDiff >= 1) {
              return dateString + ` (${yearDiff}년`.concat(diff % 12 !== 0 ? ` ${diff - yearDiff * 12}개월)` : ")");
            }

            return dateString + ` (${diff}개월)`;
          } else {
            const dateStr2 = splitDateString[1].trimLeft().split(" ");
            const year2 = dateStr2[0].match(/\d+/g);
            const month2 = dateStr2[1].match(/\d+/g);

            if (year2 && month2) {
              const parseDate2 = dayjs()
                .set("year", +year2)
                .set("month", +month2)
                .set("date", 1);

              const yearDiff2 = parseDate2.diff(parseDate, "year");
              const diff2 = parseDate2.diff(parseDate, "month");

              if (yearDiff2 >= 1) {
                return (
                  dateString + ` (${yearDiff2}년`.concat(diff2 % 12 !== 0 ? ` ${diff2 - yearDiff2 * 12}개월)` : ")")
                );
              }
              return dateString + ` (${diff2}개월)`;
            } else {
              console.log("잘못된 날짜 형식입니다");
            }
          }
        } else {
          console.log("잘못된 날짜 형식입니다");
        }
      }
      return dateString;
    },
  },
});
