<template>
  <div>
    <Card
      class="h-64 rounded-md shadow-md transition-colors duration-500 hover:bg-gray-300 tracking-tight"
      @mouseover="setMouseOver(true)"
      @mouseout="setMouseOver(false)"
    >
      <template #title>{{ title }}</template>
      <template #content>
        <transition name="slide-fade" mode="out-in">
          <div v-if="!mouseOver" class="mt-10">{{ description }}</div>
          <div v-else class="mt-10">{{ period }} <br />{{ stack }}</div>
        </transition>
      </template>
    </Card>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
  name: "Project",
  props: {
    title: String,
    description: String,
    period: String,
    stack: String,
  },
  methods: {
    setMouseOver(value: boolean) {
      this.mouseOver = value;
    },
  },
  data() {
    return {
      mouseOver: false,
    };
  },
});
</script>

<style scoped>
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.6s cubic-bezier(1, 0.5, 0.8, 1);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateY(15px);
  opacity: 0;
}
</style>
