<template>
  <div class="my-8">
    <form
      class="p-grid p-fluid container 2xl:max-w-7xl mx-auto space-y-6 px-12"
      @submit.prevent="submit"
    >
      <div class="p-col-12 p-md-4">
        <div>저에게 연락을 남기시려면</div>
      </div>
      <div class="p-col-12 p-md-4">
        <InputText
          name="email"
          v-model="email"
          class="p-inputtext-sm"
          id="email"
          type="email"
          placeholder="이메일"
        />
        <span class="text-red-800 text-left">{{
          emailError ? "정확한 이메일을 입력하세요" : ""
        }}</span>
      </div>
      <div class="p-col-12 p-md-4">
        <InputText
          name="title"
          v-model="title"
          class="p-inputtext-sm"
          id="title"
          type="text"
          placeholder="제목"
        />
        <span class="text-red-800 text-left">{{
          titleError ? "제목을 입력하세요" : ""
        }}</span>
      </div>
      <div class="p-col-12 p-md-4">
        <Textarea
          name="content"
          v-model="content"
          class="p-inputtext-sm"
          id="content"
          rows="10"
          placeholder="내용"
        />
        <span class="text-red-800 text-left">{{
          contentError ? "내용을 입력하세요" : ""
        }}</span>
      </div>
      <div class="w-32 m-auto">
        <Button class="p-button-secondary" label="전송" type="submit" />
      </div>
    </form>
  </div>
</template>

<script lang="ts">
import { defineComponent } from "@vue/runtime-core";
import { useForm, useField } from "vee-validate";
import axios from "axios";
import * as yup from "yup";

export default defineComponent({
  data() {
    return {};
  },
  setup() {
    const schema = yup.object({
      email: yup.string().required().email(),
      title: yup.string().required(),
      content: yup.string().required(),
    });

    const client = axios.create();

    const { handleSubmit } = useForm({
      validationSchema: schema,
    });

    const { value: email, errorMessage: emailError } = useField("email");
    const { value: title, errorMessage: titleError } = useField("title");
    const { value: content, errorMessage: contentError } = useField("content");

    const submit = handleSubmit(async (values) => {
      console.log(values);
      try {
        const res = await client.post(
          "https://seonwoomail-01.azurewebsites.net/api/nodemail?code=csAdzaQrcjCO9Tq14v14swoGiCcTUZCaWxMW2bqBCP4mmYwuXRVAwA==",
          {
            email: values.email,
            title: values.title,
            content: values.content,
          },
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        );
        console.log(res.status);
        if (res.status == 200) {
          alert("전송 완료");
        }
      } catch (error) {
        console.log(error);
      }
    });

    return {
      email,
      emailError,
      title,
      titleError,
      content,
      contentError,
      submit,
    };
  },
  components: {
    // Field,
    // ErrorMessage,
  },
  methods: {
    onSubmit() {
      console.log(1);
    },
  },
});
</script>

<style scoped></style>
